<template>
  <div class="blush-thumb">
    <img v-if="!this.isVideo(this.player.getThumb())" class="thumb" v-bind:src="this.player.getThumb()">
    <!--<img v-else class="thumb" src="@/assets/video.svg">-->
    <img v-if="this.player.isVideo()" class="thumb-overlay" src="@/assets/video-overlay.svg">
    <div class="thumb-title">
      <h3>{{ player.info.title }}</h3>
    </div>
    <div class="thumb-footer">
      <p v-if="this.hasCollection()">
        {{ collection.primaryLength() }}
        <span v-if="collection.secondaryLength() > 0" >(+{{ collection.secondaryLength() }})</span>
        <img class="svg-2" src="@/assets/image.svg">
      </p>
    </div>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import SequencePlayer from "@/SequencePlayer";
import util from "@/util";
import CollectionPlayer from "@/CollectionPlayer";

@Options({
  props: {
    collection: CollectionPlayer,
    sequence: SequencePlayer
  },
  methods: {
    isVideo(url: string) {
      return util.isVideo(url)
    },
    hasCollection() {
      return this.collection != undefined
    }
  },
  created() {
    if (this.collection != undefined) {
      this.player = this.collection.activeSequence()
    } else {
      this.player = this.sequence
    }
  }
})
export default class BlushThumb extends Vue {
  collection!: SequencePlayer
  sequence!: SequencePlayer
  player!: SequencePlayer
}
</script>

