import axios from "axios";
import Hjson from "hjson";

export default {
  baseUrl(): string {
    return process.env.VUE_APP_BASE_URL
  },
  wait(ms: number) {
  return new Promise((r, j) => {
      setTimeout(r, ms)
    })
  },
  async getInfo(id: string) {
    let response = await axios.get(
      this.baseUrl() + '/' + id + '/info.hjson',
      {transformResponse: [(data) => {return data}]}) // Return pure data, do not autoconvert to json

    let data = response.data
    let info = Hjson.parse(data)
    return info
  },
  isVideo(url: string) {
    return /\.(webm|mp4)$/i.test(url)
  },
  registerDisqus(route: any) {
    /**
     * RECOMMENDED CONFIGURATION VARIABLES: EDIT AND UNCOMMENT THE SECTION BELOW TO INSERT DYNAMIC VALUES FROM YOUR PLATFORM OR CMS.
     * LEARN WHY DEFINING THESE VARIABLES IS IMPORTANT: https://disqus.com/admin/universalcode/#configuration-variables*/

    let pageUrl = this.baseUrl() + route.href.substring(1)

    // @ts-ignore
    if (window.DISQUS == undefined) {
      var disqus_config = function() {
        // @ts-ignore
        this.page.url = pageUrl; // Replace PAGE_URL with your page's canonical URL variable
        // @ts-ignore
        this.page.identifier = pageUrl; // Replace PAGE_IDENTIFIER with your page's unique identifier variable
      };
      // @ts-ignore
      window.disqus_config = disqus_config;

      (function() { // DON'T EDIT BELOW THIS LINE
        var d = document,
          s = d.createElement('script');
        s.src = 'https://gardamuse.disqus.com/embed.js';
        // @ts-ignore
        s.setAttribute('data-timestamp', + new Date());
        (d.head || d.body).appendChild(s);
      })();
    } else {
      // @ts-ignore
      window.DISQUS.reset({
        reload: true,
        config: function () {
          this.page.identifier = pageUrl;
          this.page.url = pageUrl;
        }
      });
    }
  }
}
